.gridWrapper {
  padding-top: 60%;
  position: relative;
}

.grid {
  background-color: var(--theme-color-1);
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;

  &.backgroundImage {
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
    background-blend-mode: soft-light;
  }
}

.itemWrapper {
  border-radius: 0.25rem;
  box-shadow: 0 0 0 2px var(--theme-color-4), 0 1px 4px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: absolute;
  z-index: 1;

  &:focus-within {
    box-shadow: 0 0 0 2px #2360c5, 0 0 0 3px rgb(255, 255, 255);
  }
}

.fullscreenStyle {
  overflow: auto;
}
