.mainBody {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--theme-color-2);
  height: 100%;
  width: 100%;
  padding: 2rem 0;

  p {
    color: #ffffff;
    margin-block-start: 0;
    margin-block-end: 0;
  }
}

.mainBodyContent {
  width: 75%;
}

.mainBodyTitle {
  font-size: 20px;
  font-weight: bold;
}

.mainBodyText {
  margin-top: 0.5rem;
}

@media (min-width: 768px) {
  .mainBodyText {
    columns: 2;
    column-gap: 2rem;
  }
}
